@charset "UTF-8";
html {
  min-height: 100%;
  background: black; }

body {
  font-family: Raleway;
  width: 100%;
  margin: auto;
  height: 100%; }

.transition--disable {
  transition: initial !important; }

/* хром, сафари */
body::-webkit-scrollbar {
  width: 0; }

/* ie 10+ */
body {
  -ms-overflow-style: none; }

/* фф (свойство больше не работает)*/
body {
  overflow: -moz-scrollbars-none; }

.content-wrapper {
  position: fixed;
  left: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 100%;
  max-width: 1080px;
  max-height: 1920px;
  background-color: #25283d;
  overflow: hidden;
  box-sizing: border-box;
  padding: 1px;
  min-height: 1920px;
  overflow-x: hidden; }
  .content-wrapper--relative {
    max-height: initial;
    overflow: visible; }

.header {
  transition: all ease-in-out 0.5s;
  position: absolute;
  transform: translateY(-110%);
  top: 0;
  left: 0;
  color: white;
  font-size: 100px;
  line-height: 53px;
  font-weight: 900;
  width: 100%;
  background-color: #142664;
  z-index: 99; }
  .header--show {
    transform: translateY(0%); }
  .header__image {
    width: 100%; }

.slider {
  opacity: 1;
  transition: opacity ease-in-out 0.5s;
  position: absolute;
  top: 0;
  left: 0;
  transition-delay: .4s;
  width: 100%;
  height: 100%;
  overflow: hidden; }
  .slider__item-list {
    position: absolute;
    top: 0;
    width: 100%;
    text-align: center; }
  .slider__item {
    transition: all ease-in-out .8s;
    transform: translateY(0);
    text-align: left;
    margin: 5% 10% 5%;
    background-color: #e30359;
    display: inline-block;
    position: relative;
    width: 800px;
    height: 800px; }
    .slider__item--active .slider__image {
      transition: all ease-in-out 0.5s;
      transform: scale(0.8); }
  .slider__image {
    transition: all ease-in-out 0.5s;
    transform: scale(1);
    line-height: 0;
    overflow: hidden; }
    .slider__image img {
      width: 100%; }
  .slider__text-wrapper {
    margin-left: -4px;
    box-sizing: border-box;
    z-index: 1;
    width: 100%;
    color: #ffd287;
    letter-spacing: -2px;
    font-size: 90px;
    line-height: 64px;
    font-weight: 900;
    position: absolute; }
    .slider__text-wrapper--shadow {
      text-shadow: 0px 0px 33.06px rgba(0, 0, 0, 0.67); }
  .slider--hide {
    opacity: 0; }
    .slider--hide .slider__item:nth-child(1) {
      transition-delay: 0.05s;
      transform: translateY(-50%); }
    .slider--hide .slider__item:nth-child(2) {
      transition-delay: 0.1s;
      transform: translateY(-50%); }
    .slider--hide .slider__item:nth-child(3) {
      transition-delay: 0.15s;
      transform: translateY(-50%); }
    .slider--hide .slider__item:nth-child(4) {
      transition-delay: 0.2s;
      transform: translateY(-50%); }

.alert {
  transition: all ease-in-out 0.5s;
  position: fixed;
  top: 300px;
  left: 0;
  right: 0;
  margin: auto;
  max-width: 210px;
  z-index: 99;
  background-color: #9b8579;
  color: white;
  font-weight: 600;
  font-size: 28px;
  text-align: center;
  padding: 30px;
  opacity: 0; }
  .alert__text-wrapper {
    color: white; }
  .alert__image {
    margin: 20px 0px; }
  .alert--active {
    top: 240px;
    opacity: 1; }

.product {
  transition: all ease-in-out 0.5s;
  background-color: white;
  opacity: 0;
  transform: translateY(40%);
  margin: 10% 10%;
  padding: 40px; }
  .product__title {
    font-weight: 900;
    font-size: 85px;
    line-height: 60px;
    padding-bottom: 30px; }
  .product--show {
    opacity: 1;
    transform: translateY(0%); }
  .product__description {
    margin-top: 30px;
    font-size: 36px; }
  .product ul {
    list-style: none; }
  .product li {
    position: relative; }
    .product li:after {
      position: absolute;
      left: -4%;
      top: 18px;
      content: " ";
      width: 10px;
      height: 10px;
      background-color: #e30359; }
